/**
* 左边菜单
*/ 
<template>
  <el-menu default-active="2" :collapse="collapsed" collapse-transition router :default-active="$route.path" unique-opened class="el-menu-vertical-demo" background-color="#15181e" text-color="#fff" active-text-color="#ffd04b">
    <div class="logobox">
      <img class="logoimg" src="../assets/img/menulogo.png" alt="">
    </div>
    <el-submenu v-for="menu in allmenu" :key="menu.menuid" :index="menu.menuname">
      <template slot="title">
        <i class="iconfont" :class="menu.icon"></i>
        <span>{{menu.menuname}}</span>
      </template>
      <el-menu-item-group>
        <el-menu-item v-for="chmenu in menu.menus" :index="'/'+chmenu.url" :key="chmenu.menuid">
          <i class="iconfont" :class="chmenu.icon"></i>
          <span>{{chmenu.menuname}}</span>
        </el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>
<script>
import { menu } from '../api/userMG'
export default {
  name: 'leftnav',
  data() {
    return {
      collapsed: false,
      allmenu: []
    }
  },
  // 创建完毕状态(里面是操作)
  created() {
    // 获取图形验证码
    let res = {
      success: true,
      data: [
        {
          menuid: 1,
          icon: 'li-icon-xiangmuguanli',
          menuname: '数据查验',
          hasThird: null,
          url: null,
          menus: [
            {
              menuid: 21,
              icon: 'li-icon-shujujiankong',
              menuname: '帮贡账号数据',
              hasThird: 'N',
              url: 'counts/Counts',
              menus: null
            },
            {
              menuid: 22,
              icon: 'li-icon-shujujiankong',
              menuname: '嫌疑数据',
              hasThird: 'N',
              url: 'counts/xycounts',
              menus: null
            },
            {
              menuid: 23,
              icon: 'li-icon-shujujiankong',
              menuname: '封禁账号数据',
              hasThird: 'N',
              url: 'counts/fjcounts',
              menus: null
            },
          ]
        },

      ],
      msg: 'success'
    }
          this.allmenu = res.data
    
    this.$root.Bus.$on('toggle', value => {
      this.collapsed = !value
    })
  }
}
</script>
<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 240px;
  min-height: 400px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  border: none;
  text-align: left;
}
.el-menu-item-group__title {
  padding: 0px;
}
.el-menu-bg {
  background-color: #1f2d3d !important;
}
.el-menu {
  border: none;
  background-color: #141820 !important;
}
.el-submenu__title {
  padding-left: 15px !important;
}
.el-submenu__title:hover {
  background-color: rgb(5, 175, 243) !important;
}
.logobox {
  height: 40px;
  line-height: 40px;
  color: #9d9d9d;
  font-size: 20px;
  text-align: center;
  padding: 20px 0px;
}
.logoimg {
  height: 40px;
}
</style>


