      <el-table-column  prop="clststus" label="处理状态" width="100">
      </el-table-column>
      <el-table-column  prop="reasoncode" label="原因编号" width="100">
      </el-table-column>
      <el-table-column  prop="fslaiyuan" label="封停来源" width="100">
      </el-table-column>
      <el-table-column  prop="reason" label="封停原因" width="100">
      </el-table-column>
      <el-table-column  prop="reasonnote" label="原因说明" width="100">
      </el-table-column>
      <el-table-column align="center"  fixed="right" label="操作" min-width="100">
        <template slot-scope="scope">
          <el-button type="success" @click="deleteUser(scope.row)">解封</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <el-pagination @size-change="handleSizeChange"  @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pageSize" :total="filteredData.length" layout="total, sizes, prev, pager, next, jumper"></el-pagination>
