// 导入组件
import Vue from 'vue';
import Router from 'vue-router';
// 登录
import login from '@/views/login';
// 首页
import index from '@/views/index';
// 数据查验
import Counts from '@/views/counts/Counts';
import xycounts from '@/views/counts/xycounts';
import fjcounts from '@/views/counts/fjcounts';
// 异常账号管理
import countmanage from '@/views/manage/countmanage';
// 安全监控
import safesecure from '@/views/system/safesecure';

// 启用路由
Vue.use(Router);

// 导出路由 
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/login',
        name: '登录',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/index',
        name: '首页',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [{
            path: '/counts/Counts',
            name: '数据查验',
            component: Counts,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/counts/xycounts',
            name: '数据查验',
            component: xycounts,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/counts/fjcounts',
            name: '数据查验',
            component: fjcounts,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/manage/countmanage',
            name: '异常账号管理',
            component: countmanage,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/system/safesecure',
            name: '安全监控',
            component: safesecure,
            meta: {
                requireAuth: true
            }
        }, 
    ]
    }]
})


