/**
 * 数据查验界面
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">数据查验</el-breadcrumb-item>
      <el-breadcrumb-item>嫌疑数据</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="searchForm" class="search-form">
      <el-form-item label="搜索：">
        <el-input type="text" size="small" v-model="searchText.countname" placeholder="输入账号名"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="searchText.roleid" placeholder="输入角色ID"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" @click="handleSearch">查询</el-button>
        <el-button size="small" type="primary" @click="handleReset">重置</el-button>
      </el-form-item>
    </el-form>
    <el-form :inline="true" :model="formInline" class="user-warnning">
      <el-form-item>
        <el-button size="small" type="danger" >封禁</el-button>
        <el-button size="small" type="success">加入白名单</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="mini" :data="filteredData.slice((currentPage-1)*pageSize, currentPage*pageSize)" :filter-method="handleFilter" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="selection" width="30">
      </el-table-column>
      <el-table-column  prop="sever" label="区服" width="100" :filters="nameFilters" :filter-method="filterByName">
      </el-table-column>
      <el-table-column  prop="countname" label="账号名" width="100">
      </el-table-column>
      <el-table-column  prop="namecount" label="账号名出现次数" width="100">
      </el-table-column>
      <el-table-column  prop="roleid" label="角色id" width="100">
      </el-table-column>
      <el-table-column  prop="rolename" label="角色名" width="100">
      </el-table-column>
      <el-table-column  prop="rolecareer" label="角色职业" width="100">
      </el-table-column>
      <el-table-column sortable prop="rolelevel" label="角色等级" width="100">
      </el-table-column>
      <el-table-column sortable prop="outtime" label="最后下线时间" width="100">
      </el-table-column>
      <el-table-column sortable prop="money_30" label="30天消耗金额" width="100">
      </el-table-column>
      <el-table-column sortable prop="summoney" label="累计消耗金额" width="100">
      </el-table-column>
      <el-table-column sortable prop="evalue" label="装备价值" width="100">
      </el-table-column>
      <el-table-column sortable prop="bagvalue" label="背包价值" width="100">
      </el-table-column>
      <el-table-column prop="safephone" label="安全手机" width="100">
      </el-table-column>
      <el-table-column prop="safephonecount" label="安全手机次数" width="100">
      </el-table-column>
      <el-table-column prop="id" label="身份证" width="100">
      </el-table-column>
      <el-table-column prop="idcount" label="身份证次数" width="100">
      </el-table-column>
      <el-table-column prop="realname" label="真实姓名" width="100">
      </el-table-column>
      <el-table-column prop="lastip" label="最后登录ip" width="100">
      </el-table-column>
      <el-table-column prop="lastipcount" label="最后登录ip次数" width="100">
      </el-table-column>
      <el-table-column prop="lastmac" label="最后登录mac" width="100">
      </el-table-column>
      <el-table-column sortable prop="taskcount" label="任务完成-总次数" width="100">
      </el-table-column>

      
      <el-table-column align="center"  fixed="right" label="操作" min-width="100">
        <template slot-scope="scope">
          <el-button type="danger" @click="deleteUser(scope.row)">封禁</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <el-pagination @size-change="handleSizeChange"  @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pageSize" :total="filteredData.length" layout="total, sizes, prev, pager, next, jumper"></el-pagination>
  </div>
</template>

<script>
export default {
    computed: {
        deletedData() {
            return this.$store.state.originalData
    }
  },
    data() {
    return {
      // 搜索条件
      searchText: {
        countname: "",
        roleid: ""
      },
      currentPage: 1, // 当前页码
      pageSize: 10,// 每页显示的数据条数
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      userparm: [], //搜索权限
      // 查询条件
      nameFilters: [],
    //   deletedData: [
    //   {
    //         sever: '荣耀24区1组',
    //         countname: 'test001',
    //         namecount: '10',
    //         roleid: '0001',
    //         rolename: '眉目添风霜',
    //         rolecareer: '青城',
    //         rolelevel: '54',
    //         outtime: '2023-02-26 18:01:23',
    //         money_30: '',
    //         summoney: '',
    //         evalue: '0.0',
    //         bagvalue: '13.09',
    //         safephone: '159****4286',
    //         afephonecount: '10',
    //         id: '232126********0562',
    //         idcount: '10',
    //         realname: '李*',
    //         lastip: '60.219.155.249',
    //         lastipcount: '10',
    //         lastmac: '76012d516f62',
    //         taskcount: '12',
    //       },
    //       {
    //         sever: '荣耀24区1组',
    //         countname: 'test002',
    //         namecount: '10',
    //         roleid: '0002',
    //         rolename: '眉目添风霜',
    //         rolecareer: '青城',
    //         rolelevel: '54',
    //         outtime: '2023-02-26 18:01:23',
    //         money_30: '',
    //         summoney: '',
    //         evalue: '0.0',
    //         bagvalue: '13.09',
    //         safephone: '159****4286',
    //         afephonecount: '10',
    //         id: '232126********0562',
    //         idcount: '10',
    //         realname: '李*',
    //         lastip: '60.219.155.249',
    //         lastipcount: '10',
    //         lastmac: '76012d516f62',
    //         taskcount: '12',
    //       },
    //       {
    //         sever: '荣耀24区1组',
    //         countname: 'test003',
    //         namecount: '10',
    //         roleid: '0003',
    //         rolename: '眉目添风霜',
    //         rolecareer: '青城',
    //         rolelevel: '54',
    //         outtime: '2023-02-26 18:01:23',
    //         money_30: '',
    //         summoney: '',
    //         evalue: '0.0',
    //         bagvalue: '13.09',
    //         safephone: '159****4286',
    //         afephonecount: '10',
    //         id: '232126********0562',
    //         idcount: '10',
    //         realname: '李*',
    //         lastip: '60.219.155.249',
    //         lastipcount: '10',
    //         lastmac: '76012d516f62',
    //         taskcount: '12',
    //       },
    //   ],
      filteredData: []
    };

  },
  created() {
    this.filteredData = [...this.deletedData] // 初始化为data数组的副本
  },

  mounted() {
    this.initFilters();
    this.total = this.filteredData.length;
  },

  methods: {
    initFilters() {
      // 生成区服筛选器选项
      this.nameFilters = this.generateFilters('sever');
    },
    generateFilters(prop) {
      let filters = [];
      // 遍历数据源中当前列的所有数据
      this.filteredData.forEach(item => {
        if (filters.indexOf(item[prop]) === -1) {
          // 如果当前选项还未添加到filters数组中，则添加
          filters.push(item[prop]);
        }
      });
      // 将选项数组按字母顺序排序
      filters.sort();
      // 将选项数组转换为filters数组
      return filters.map(item => {
        return {
          text: item,
          value: item
        };
      });
    },
    filterByName(value, row) {
      // 如果选项为“全部”，则返回所有行
      if (value.length === 0) {
        return true;
      }
      // 否则，只返回与选项匹配的行
      return value.indexOf(row.sever) !== -1;
    },

    handleFilter(value, row, column) {
      if (!value) {
      // 如果查询条件为空，则返回所有数据
        return true;
      } else {
      // 如果查询条件不为空，则根据条件筛选数据
        const prop = column['property'];
        const val = row[prop];
        return val.indexOf(value) !== -1;
      }
    },

    handleSearch() {
      const { countname, roleid} = this.searchText;
    // 调用表格组件的 filter 方法进行过滤操作
      this.filteredData = this.filteredData.filter((item) => 
        (countname === "" || item.countname.includes(countname)) &&
        (roleid === "" || item.roleid.toString().includes(roleid)));
    // 将过滤后的数据保存到组件的data选项中
    },

    handleReset() {
    // 重置查询条件并清空过滤结果
      this.searchText.countname = '';
      this.searchText.roleid = '';
      this.filteredData = this.deletedData// 将数据表数据重置为初始数据
    },

    deleteUser(index, row) {
      this.$confirm('确定要封禁吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          countDelete(row.count2)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: '已封禁!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('封禁失败，请稍后再试！')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消封禁'
          })
        })
    },

    handleSizeChange(val) {
      this.pageSize = val
      this.currentPage = 1 // 切换每页显示条数时，当前页码重置为 1
    },

    handleCurrentChange(val) {
      this.currentPage = val
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


